.selected-feature {
  fill: red;
  cursor: pointer;
}

.unselected-feature {
  fill: black;
  cursor: pointer;
}

.Top {
  display: flex;
  width: 100%;
}

.Header {
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border-bottom: solid lightgrey 1px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-size: 150%;
  font-weight: bold;
}

.GlossesList {
  width: 300px;
  overflow-y: auto;
  margin-left: 10px;
  margin-right: 10px;
  padding-right: 10px;
  border-right: solid lightgrey 1px;
}

.Citation {
  width: 500px;
  border-right: solid lightgrey 1px;
  padding-right: 10px;
}

.CitationPlayer {
    width: 500px;
    padding-right: 10px;
}

.TextInfo {
  width: 800px;
  margin-left: 10px;
  margin-right: 10px;
  overflow-y: auto;
}

.GlossTexts {
  border-bottom: solid lightgrey 1px;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

.Gloss {
    margin-bottom: 5px;
}

.GlossSelected {
    background-color: lightblue
}

.GlossText {
    margin-bottom: 5px;
}

.SentenceTexts {
    font-size: 100%;  /* place holder to avoid lint complaining about 'empty' css */
}

.SentenceText {
    margin-bottom: 5px;
}
